
import React from "react";
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Avatar from '@material-ui/core/Avatar';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import CssBaseline from '@material-ui/core/CssBaseline';
import Content from "./Content";
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import FolderOpenIcon from '@material-ui/icons/FolderOpen';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import NotificationsNoneIcon from '@material-ui/icons/NotificationsNone';
import SearchIcon from '@material-ui/icons/Search';
import SettingsOutlinedIcon from '@material-ui/icons/SettingsOutlined';
import ShowChartIcon from '@material-ui/icons/ShowChart';
import StorageIcon from '@material-ui/icons/Storage';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import WebAssetIcon from '@material-ui/icons/WebAsset';

const drawerWidth = 250;

const useStyles = makeStyles((theme) => ({
    appBar: {
        marginLeft: theme.spacing(7) + 1,
        width: `calc(100% - ${theme.spacing(7) + 1}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
    },
    avatar: {
        width: theme.spacing(7),
        height: theme.spacing(7),
        marginRight: theme.spacing(1),
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
        whiteSpace: 'nowrap',
    },
    drawerClose: {
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        overflowX: 'hidden',
        width: theme.spacing(7) + 1,
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(7) + 1,
        },
    },
    drawerOpen: {
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    logoImg: {
        display: 'block',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: '4em',
    },
    root: {
        display: 'flex',
    },
    searchButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    toolbar: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
    },
}));

export default function App() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleDrawerOpenClose = () => {
        setOpen(!open);
    };

    return (
        <div className={classes.root}>
            <CssBaseline />
            <AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                    [classes.appBarShift]: open,
                })}
            >
                <Toolbar>
                    <IconButton
                        edge="start"
                        className={classes.searchButton}
                        color="inherit"
                        aria-label="search"
                    >
                        <SearchIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title} />
                    <Avatar alt="Константинопольская Е.Л." src="./images/avatar.png" className={classes.avatar} />
                    <div>
                        <Typography variant="h6">Константинопольская Е.Л.</Typography>
                        <Typography>ООО "Трейдонлайн"</Typography>
                    </div>
                </Toolbar>
            </AppBar>

            <Drawer
                variant="permanent"
                className={clsx(classes.drawer, {
                    [classes.drawerOpen]: open,
                    [classes.drawerClose]: !open,
                })}
                classes={{
                    paper: clsx({
                        [classes.drawerOpen]: open,
                        [classes.drawerClose]: !open,
                    }),
                }}
            >
                <img src="./images/logo.png" alt="logo" className={classes.logoImg} />

                <List>
                    <ListItem button key='Архив'>
                        <ListItemIcon><StorageIcon /></ListItemIcon>
                        <ListItemText primary='Архив' />
                    </ListItem>
                    <ListItem button key='Рабочий стол'>
                        <ListItemIcon><WebAssetIcon /></ListItemIcon>
                        <ListItemText primary='Рабочий стол' />
                    </ListItem>
                    <ListItem button key='Рассмотрение заявок' selected={true}>
                        <ListItemIcon><ShowChartIcon /></ListItemIcon>
                        <ListItemText primary='Рассмотрение заявок' />
                    </ListItem>
                    <ListItem button key='Уведомления'>
                        <ListItemIcon><NotificationsNoneIcon /></ListItemIcon>
                        <ListItemText primary='Уведомления' />
                    </ListItem>
                    <ListItem button key='Картотека'>
                        <ListItemIcon><FolderOpenIcon /></ListItemIcon>
                        <ListItemText primary='Картотека' />
                    </ListItem>
                    <ListItem button key='Подписанные'>
                        <ListItemIcon><CheckCircleOutlineIcon /></ListItemIcon>
                        <ListItemText primary='Подписанные' />
                    </ListItem>
                    <ListItem button key='Настройки'>
                        <ListItemIcon><SettingsOutlinedIcon /></ListItemIcon>
                        <ListItemText primary='Настройки' />
                    </ListItem>
                </List>
                <Divider />
                <div className={classes.toolbar}>
                    <IconButton onClick={handleDrawerOpenClose}>
                        {open ? <ChevronLeftIcon /> : <ChevronRightIcon />}
                    </IconButton>
                </div>
            </Drawer>

            <Content />
        </div>
    );
}
